<template>
    <div>
        <div style="height: 80px">
            <detail-header>
                <el-input placeholder="请输入内容" class="input-with-select" type="text">
                    <el-select v-model="select" slot="prepend">
                        <el-option label="图片" value="1"></el-option>
                        <el-option label="用户" value="2"></el-option>
                        <el-option label="评论" value="3"></el-option>
                    </el-select>
                    <el-button slot="append" class="iconfont icon-sousuo"></el-button>
                </el-input>
            </detail-header>
        </div>
        <div class="user-tab">
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane label="用户信息" name="info"></el-tab-pane>
                <el-tab-pane label="作品" name="works"></el-tab-pane>
                <el-tab-pane label="收藏" name="favorites"></el-tab-pane>
            </el-tabs>
        </div>

        <router-view></router-view>

    </div>
</template>

<script>
    import DetailHeader from "../components/DetailHeader";
    import {getCookie} from "../../utils/cookies";
    export default {
        name: "index",
        components: {
            DetailHeader
        },
        created() {
          this.activeName = this.$route.path.split('/')[2]
        },
        updated() {
            this.activeName = this.$route.path.split('/')[2]
        },
        data() {
            return {
                activeName: '',
                select: '',
                userId: this.$store.getters.consumerInfo.id
            };
        },
        methods: {
            handleClick(tab) {
                this.activeName = tab.paneName
                this.$router.push(`/accounts/${tab.paneName}/${this.userId}`)
            }
        }
    }
</script>

<style scoped lang="scss">
    .user-tab{
        top: 80px;
        width: 80%;
        margin: 0 auto;

    }

</style>
