// 路由守卫
import router from '@/router';
import store from '@/store';
import { getCookie } from '@/utils/cookies';
import { urlQueryParse } from '@/utils/index';
import { login } from '@/service/login-api';
// import { DailyPicLogin } from './path';

// 免登陆白名单:  所有人都有权限登录的页面
const whiteList = ['/login', '/', '/home', '/register', '/about', '/topic'];

router.beforeEach((to, from, next) => {
    // next();
    if (whiteList.includes(to.path) || to.path.startsWith('/detail')){
        next()
        return
    }

    // todo :: 需要重构
    const userToken = urlQueryParse(location.search.split('?')[1]).userToken;

    if (userToken) {
        // 去登录
        login({ user_token: userToken })
            .then(res => {
                // res.data = {
                //   jwt: 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJleHAiOjE2MTY2OTEzNDYsInVzZXJfaWQiOjEwLCJ1c2VyX3R5cGUiOjF9.ohXLbxYRfWoLxL3p103P-zjoMr7CUsW9XoS9HyOnbH0',
                //   login_url: 'http://aido-admin-business.test.flkj.local/',
                //   res_code: true
                // };

                const data = res.data;

                // 登录成功
                if (data && data.login_url) {
                    localStorage.setItem("FOSS_LOGIN_URL", data.login_url);
                }
                if (data.res_code === true) {
                    store.dispatch('setJwt', {
                            jwt: data.jwt,
                            loginUrl: data.login_url
                        })
                        .then(() => {
                            location.href = '/';
                        });
                } else {
                    location.href = data.login_url;
                }
            })
            .catch(() => {
                // todo:: http请求失败
            });
    } else {
        // 判断是否登录

        if (getCookie('jwt')) {
            if (!store.state.user.username) {
                // 重新获取用户信息
                // getUserInfo()
                //     .then(res => {
                        // admin = 0, '业务管理员';
                        // business_dev = 1, 'BD';
                        // planner = 2, '企划';
                        // anchor = 3, '主播';
                        // goods_ops = 4, '商品运营'

                        // res.data = {
                        //     user_name: 'anchor4',
                        //     user_icon: null,
                        //     phone_number: '104',
                        //     user_code: 'anchor4',
                        //     identity: 0,
                        //     identity_name: '业务管理员',
                        //     last_login_time: 1616080567
                        // };
                    //     store.commit('setUserInfo', res.data);
                    //     store.dispatch('setName', res.data.user_name);
                    //     next();
                    // });
            } else {
                next();
            }
        } else {
            let loginUrl = store.state.user.loginUrl;
            if (loginUrl) {
                localStorage.setItem("FOSS_LOGIN_URL", loginUrl);
            } else {
                loginUrl = localStorage.getItem("FOSS_LOGIN_URL");
                // set to default login url
                if (!loginUrl)
                    loginUrl = "/login";
            }
            location.href = loginUrl;
        }
    }
});
