// eslint-disable-next-line no-unused-vars
import { Message } from 'element-ui';

function message (type, message) {
  Message({
    showClose: true,
    duration: 2000,
    message,
    type
  });
}

function messageWithDuration (type, message, showDurationMs) {
  Message({
    showClose: true,
    duration: showDurationMs,
    message,
    type
  });
}

export function megSuc (txt) {
  message('success', txt);
}

export function megError (txt) {
  message('error', txt);
}

export function megWarn (txt) {
  messageWithDuration('warning', txt, 5000);
}

export function megInfo (txt) {
  message('info', txt);
}
