// url参数解析
export function urlQueryParse (str) {
  const resObj = {};
  let arr = [];

  if (Object.prototype.toString.call(str) === '[object String]') {
    arr = str.split('&');
    arr.forEach(item => {
      const obj = item.split('=');
      resObj[decodeURIComponent(obj[0])] = decodeURIComponent(obj[1]);
    });
  }

  return resObj;
}

