import http from './http'

/**
 * 账号登录
 * @export
 * @param {Object} data -
 * @param {string} data.username - 用户名
 * @param {string} data.password - 密码
 * @returns
 */
export async function login(data){
    return await http.request({
        url: '/login',
        method: 'post',
        data
    });
}

/**
 * 账号登出
 */
export async function login_out(){
    return await http.request({
        url: '/logout',
        method: 'delete'
    })
}
