<template>
    <ul :class="{menu0: !scroll,
    menu1: scroll}">

        <li><router-link :to="{name: 'home'}" :class="{active0: !scroll && checkName === '图片',
        active1: scroll && checkName === '图片'}" @click.native="changeCheckName('图片')">图片</router-link><span v-if="checkName === '图片'"></span></li>
        <li><router-link :to="{name: 'topic'}" :class="{active0: !scroll && checkName === '吐槽',
        active1: scroll && checkName === '吐槽'}" @click.native="changeCheckName('吐槽')">吐槽</router-link><span v-if="checkName === '吐槽'"></span></li>
        <li><router-link :to="{name: 'about'}" :class="{active0: !scroll && checkName === '关于',
        active1: scroll && checkName === '关于'}" @click.native="changeCheckName('关于')">关于</router-link><span v-if="checkName === '关于'"></span></li>
        <li v-if="isLogin"><router-link :to="{name: 'publish'}" :class="{active0: !scroll && checkName === '上传作品',
        active1: scroll && checkName === '上传作品'}" @click.native="changeCheckName('上传作品')">上传作品</router-link><span v-if="checkName === '上传作品'"></span></li>

    </ul>
</template>

<script>
    import { mapGetters } from 'vuex';
    export default {
        name: "MyNav",
        props: ['scroll'],
        computed: {
            ...mapGetters([
                "checkName",
                "userName"
            ]),
        },
        created() {
            this.isLogin = !!this.userName

        },
        data(){
            return {
                isLogin: false
            }
        },
        methods: {
            changeCheckName(name) {
                this.$store.dispatch('setCheckName', name)
            }
        }
    }
</script>

