        padding:0;
        li{
            width:100px;
            height:72px;
            float:left;
            margin-left:-1px;
            display: flex;
            justify-content: center;
            align-items: center;
            a{
                display:block;
                width:98px;
                height:72px;
                text-align:center;
                line-height:72px;
                text-decoration:none;
                font-size: 20px;
                font-family:'Microsoft Yahei';
                font-weight: bold;
                color: rgba(255, 255, 255, .8);
                &:hover{
                    color: #fff;
                }
            }
            .active0{
                color: #fff;
            }
            span{
                position: absolute;
                width: 4px;
                height: 4px;
