<template>
    <div>
        <div class="header stage1" id="header">
            <el-row>
                <el-col :span="3" :offset="1">
                    <div class="header__logo creative_header__logo" @click="redirectIndex"></div>
                </el-col>
                <el-col :span="13" :offset="1">
                    <slot></slot>
                </el-col>
                <el-col :span="5" :offset="1" class="avatar-box" v-show="isLogin">

                    <el-dropdown>
                        <div class="avatar">
                            <el-avatar :size="50" :src="consumerInfo.avatar">
                                <img src="https://cube.elemecdn.com/e/fd/0fc7d20532fdaf769a25683617711png.png"/>
                            </el-avatar>
                        </div>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item><router-link :to="{name: 'space', params:{id: consumerInfo.id}}">个人中心</router-link></el-dropdown-item>
                            <el-dropdown-item><a @click="logout">退出</a></el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                    <span>{{ userName }}</span>

                </el-col>
                <el-col :span="5" :offset="1" class="avatar-box" v-show="!isLogin"
                        :style="{color: '#000000'}">
                    <router-link :to="{name: 'login'}" :style="{color: '#000000'}">登录</router-link>
                    <span>|</span>
                    <router-link :to="{name: 'register'}" :style="{color: '#000000'}">注册</router-link>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import {login_out} from "../../service/login-api";
    import {megSuc} from "../../utils/message";
    import {getCookie} from "../../utils/cookies";
    export default {
        name: "DetailHeader",
        inject: ['path'],
        computed: {
            ...mapGetters([
                "consumerInfo",
                "userName"]),
        },
        created() {
            this.isLogin = !!this.userName
        },
        data(){
            return {
                select: '图片',
                isLogin: false
            }
        },
        methods: {
            logout(){
                console.log('ddd')
                login_out()
                    .then((res) => {
                        if (res.code === 2000){
                            this.$store.dispatch('logOut')
                                .then(() => {
                                    megSuc('退出成功!')
                                    this.isLogin = false
                                    this.$router.push('/home')
                                })
                        }
                    })
            },
            redirectIndex(){
                this.$router.push(this.path.Index())
            }
        }
    }
</script>

<style scoped lang="scss">
    .avatar{
        height: 55px;
        width: 72px;
    }

</style>
