<template>
    <div>
        <div :class="{header: true,
                  stage0: !scroll,
                  stage1: scroll}" id="header">
            <el-row>
                <el-col :span="3" :offset="1">
                    <div class="header__logo creative_header__logo"></div>
                </el-col>
                <el-col :span="14">
                    <my-nav :scroll="scroll"></my-nav>
                </el-col>
                <el-col :span="5" :offset="1" class="avatar-box" v-show="isLogin">

                    <el-dropdown>
                        <div class="avatar">
                            <el-avatar :size="50" :src="consumerInfo.avatar">
                                <img src="https://cube.elemecdn.com/e/fd/0fc7d20532fdaf769a25683617711png.png"/>
                            </el-avatar>
                        </div>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item><router-link :to="{name: 'info', params:{id:consumerInfo.id}}">个人中心</router-link></el-dropdown-item>
                            <el-dropdown-item><a @click="logout">退出</a></el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                    <span>{{ userName }}</span>

                </el-col>
                <el-col :span="5" :offset="1" class="avatar-box" v-show="!isLogin"
                        :style="scroll?{color: '#000000'}:{color: '#fff'}">
                    <router-link :to="{name: 'login'}" :style="scroll?{color: '#000000'}:{color: '#fff'}">登录</router-link>
                    <span>|</span>
                    <router-link :to="{name: 'register'}" :style="scroll?{color: '#000000'}:{color: '#fff'}">注册</router-link>
                </el-col>
            </el-row>
        </div>
        <div class="carousel">
            <el-carousel :interval="5000" arrow="always" height="530px">
                <el-carousel-item v-for="item in imgUrls" :key="item.id">
                   <img :src="item.url" />
                </el-carousel-item>

            </el-carousel>
        </div>
        <div class="search">
            <el-input placeholder="请输入内容" class="input-with-select" type="text">
                <el-select v-model="select" slot="prepend">
                    <el-option label="图片" value="1"></el-option>
                    <el-option label="用户" value="2"></el-option>
                    <el-option label="评论" value="3"></el-option>
                </el-select>
                <el-button slot="append" class="iconfont icon-sousuo"></el-button>
            </el-input>
        </div>
    </div>

</template>
<script>
    import MyNav from "./MyNav";
    import { mapGetters } from 'vuex';
    import { login_out } from "../../service/login-api";
    import { megSuc} from "../../utils/message";

    export default {
      name: 'AppHeader',
      inject: ['path'],
      computed: {
          ...mapGetters([
              "consumerInfo",
              "userName"]),
      },
      created() {
          this.isLogin = !!this.userName

      },
      data() {
        return {
            userInformation: {},
            select: '图片',
            scroll: false,
            isLogin: false,
            imgUrls: [{
                id: 1,
                url: 'https://s1.tuchong.com/content-image/202104/fe7dcd931b140180ce3b612df4b131b8.jpg'
            },
                {
                    id: 2,
                    url: 'https://s1.tuchong.com/content-image/202104/3e87f725c1b4709509e56cc5288c960b.jpg'
                }]
        }
      },
      components:{
          MyNav
      },
      mounted() {
          this.$nextTick(() => {
              window.addEventListener("scroll", this.handleScroll);
          });
        },
      destroyed() {
          window.removeEventListener('scroll', this.handleScroll);
      },
      methods: {
          handleScroll() {
              let scrollTop =
                    window.pageYOffset ||
                    document.documentElement.scrollTop ||
                    document.body.scrollTop;
              this.scroll = scrollTop > 10;
          },
          logout(){
              console.log('ddd')
              login_out()
                  .then((res) => {
                      if (res.code === 2000){
                          this.$store.dispatch('logOut')
                              .then(() => {
                                  megSuc('退出成功!')
                                  this.$router.go(0);
                                  this.isLogin = false
                              })
                      }
                  })


          }
        }
  }

</script>
