import _import from './_import';
import Space from '../views/user-space/index'

export default [{
    path: '/accounts',
    title: '个人空间',
    name: 'space',
    component: Space,
    redirect: {
        name: 'info'
    },
    meta: {
        login: true
    },
    children: [
        {
            path: 'info/:id',
            name: 'info',
            title: '个人中心',
            component: _import('user-space/user-info'),
            meta: {
                login: true
            },
        },
        {
            path: 'works/:id',
            name: 'works',
            title: '作品',
            component: _import('user-space/works'),
            meta: {
                login: true
            },
        },
        {
            path: 'favorites/:id',
            name: 'favorites',
            title: '收藏',
            component: _import('user-space/favorites'),
            meta: {
                login: true
            },
        },
        // {
        //     path: 'collection',
        //     name: 'collection',
        //     title: '收藏',
        //     component: _import('user-space/fans'),
        //     meta: {
        //         login: true
        //     },
        // }
    ]
},]
