import { setCookie, getCookie, removeCookie } from '@/utils/cookies';


const user = {
    state: {
        username: getCookie('userName') || '',
        jwt: getCookie('jwt') || '',
        consumerInfo: getCookie('userInfo')?JSON.parse(getCookie('userInfo')):{} // 用户信息
    },
    mutations: {
        setName: (state, name) => {
            state.username = name;
        },

        setJwt: (state, jwt) => {
            state.jwt = jwt;
        },
        setConsumerInfo: (state, data) => {
            state.consumerInfo = data;
        },

        resetAllState: (state) => {
            removeCookie('userName');
            removeCookie('jwt');
            removeCookie('sessionid');
            removeCookie('userInfo')
            state.username = '';
            state.jwt = '';
            state.userInfo = {};
        }
    },
    actions: {
        setJwt: ({ commit }, data) => {
            return new Promise(resolve => {
                setCookie('jwt', data.jwt, { expires: 2 });
                commit('setJwt', data.jwt);
                resolve();
            });
        },

        setName: ({ commit }, name) => {
            return new Promise(resolve => {
                setCookie('userName', name, { expires: 2 });
                commit('setName', name);
                resolve();
            });
        },
        setUserInfo: ({ commit }, data) => {
            return new Promise(resolve => {
                setCookie('jwt', data.jwt, { expires: 2 });
                setCookie('userName', data.username, { expires: 2 });
                setCookie('userInfo', data, { expires: 2 });
                commit('setJwt', data.jwt);
                commit('setName', data.username);
                commit('setConsumerInfo', data)
                resolve();
            })
        },
        logOut: ({ commit }) => {
            return new Promise(resolve => {
                commit('resetAllState');
                resolve();
            });
        }
    }
};

export default user;
