
const user = {
    state: {
        checkName: '图片'
    },
    mutations: {
        setCheckName: (state, name) => {
            state.checkName = name;
        }
    },
    actions: {

        setCheckName: ({ commit }, name) => {
            return new Promise(resolve => {
                commit('setCheckName', name);
                resolve();
            });
        }
    }
};

export default user;
