import Vue from 'vue';
import Router from 'vue-router';
import _import from './_import';
import Layout from '@/views/components/index'
import user from './user';


Vue.use(Router);

// 页面的一些信息可以写在meta
// meta的auth不为空，表示该页面需要权限认证
// auth为假或者不存在meta则表示该页面用户可访问
// hidden控制该页面是否在侧边导航栏显示

const globalRouter = [
    {
      path: '/login',
      name: 'login',
      component: _import('user-login/login')
    },
    {
        path: '/register',
        name: 'register',
        component: _import('user-login/register')
    },
    {
        path: '/error',
        name: 'error',
        component: _import('components/error_404')
    },
    {
        path: '',
        component: Layout,
        redirect: '/home',
        children: [{
            path: '/home',
            name: 'home',
            component: _import('home/Home')
        }]
    },
    {
        path: '/detail/:id',
        name: 'detail',
        component: _import('detail/Detail')
    },
    {
        path: '*',
        component: _import('components/error_404')
    },
    {
        path: '/publish',
        name: 'publish',
        component: _import('create/publish-pic')
    },
    {
        path: '/about',
        name: 'about',
        component: _import('about/index')
    },
    {
        path: '/topic',
        name: 'topic',
        component: _import('topic/index')
    }
];


const pageRouter = [...user];

const routerMap = [...globalRouter, ...pageRouter];

export { pageRouter };

export default new Router({
    mode: 'history',
    scrollBehavior: () => ({ y: 0 }), // 页面滚动到顶部
    routes: routerMap
});
